.preloader  {
  position: fixed;
  top: 0;left: 0;
  background: #fff;
  z-index: 999999;
  width: 100%;
  height: 100%;
  color: #fff;
  overflow: hidden;
  font-family: sans-serif;
  display: flex;
  align-items: center;
  justify-content: center;
  &.hide {
    opacity: 0;
    transition: 1s;
    transition-delay: .5s;
    pointer-events: none;
  }
  &.remove {
    display: none;
  }
  img {
    animation: changefill infinite ease 4s;
  }
}

@keyframes changefill {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(4);
    opacity: 0;
  }
}
